import React, {useEffect} from "react"
import 'bootstrap/dist/css/bootstrap.min.css'
import { Container } from 'react-bootstrap';
import { Link } from "gatsby";
import { Helmet } from "react-helmet";
import { Image } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import { Navbar } from "react-bootstrap";
import { Collapse } from "react-bootstrap";
import { Row, Col, Button } from "react-bootstrap";

import newsletter2 from "../../static/newsletter2(1).png"
import newsletter1 from "../../static/newsletter1.png"
import firstimg from "../../static/Mrs. Rajamani Teacher Photo 2019-2020.png"
import secondimg from "../../static/2020 Portrait.jpg"
import eventimg from "../../static/index.jpg"
import donations from "../../static/donations.jpg"
import donations2 from "../../static/Katie_SutterHealth.jpg"

var no1Link = "https://www.canva.com/design/DAD7VsQaBBU/P0_WdDbW-GwZmh1Gw-M1uQ/view?utm_content=DAD7VsQaBBU&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var latestLink = "https://www.canva.com/design/DAEB5NGHJ5Q/f8YFMnbBYUdyZROJJxH4Ng/view?utm_content=DAEB5NGHJ5Q&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var firstLink = "https://www.canva.com/design/DAEDHBfSKfc/TZ7XXdSnt8WFeM0SMWKkcQ/view?utm_content=DAEDHBfSKfc&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var secondLink = "https://www.canva.com/design/DAEDY5RYeNA/U73XzEhDUGpMXMmUGJR-Vg/view?utm_content=DAEDY5RYeNA&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var eventLink = "https://www.canva.com/design/DAEDY1sY2HY/24LpLTpkIRhKnRb-xjgIZg/view?utm_content=DAEDY1sY2HY&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var no2Title = "Learning From Afar"
var no2Desc = "A dive into the impact of distance learning on the education system"
var no1Title = "Everyday Heroes"
var no1Desc = "A look into the everyday heroes who have been working to combat the COVID-19 pandemic"
// HAVE TO ADJUST WIDTH FOR EACH IMAGE (OR CROP)

export default function Home() {
  useEffect(() => {
    window.onscroll = function() {
      var navlinks = document.getElementsByClassName('navlink')
      if(window.pageYOffset >= 75) {
        document.getElementById('navbar').style.backgroundColor = "rgba(40,50,70,255)";
        for (var i=0;i<navlinks.length;i++) {
          navlinks[i].classList.add("thisHover")
          navlinks[i].classList.remove("hovering")
        }
      }
      else {
        document.getElementById('navbar').style.backgroundColor = "rgba(0.3,0.3,0.3,0.5)";
        for (var p=0;p<navlinks.length;p++) {
          navlinks[p].classList.add("hovering")
          navlinks[p].classList.remove("thisHover")
        }
      }
    };
  }, []);

  function openLink(e) {
    window.open(e)
  }

  return (
  <Container fluid>
    <Helmet>
        <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, minimum-scale=1.0, 
        height=device-height, target-densitydpi=device-dpi, shrink-to-fit=no, user-scalable=no" />
        <title>Articles - BAYouth</title>
        <body id="articles-page" />
    </Helmet>
    <Navbar collapseOnSelect expand="lg" fixed="top" id="navbar" style={{ backgroundColor: `rgba(0.3,0.3,0.3,0.5)` }}>
        <Navbar.Brand href="/" style={{ paddingRight: `1.5rem`, color: `white` }}>BAYouth</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
                <Nav.Link href="/articles" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Our Articles</Nav.Link>
                <Nav.Link href="/events" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Events</Nav.Link>
            </Nav>
            <Nav>
                <Nav.Link href="/meetus" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Meet the Team</Nav.Link>
            </Nav>
        </Navbar.Collapse>
    </Navbar>

    <div id="articles-body">
      <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} style={{ padding: 0, margin: `3.5rem 0` }} className="center overlay">
          </Col>
      </Row>
    </div>

    {/* <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Individual Articles</p>
        </Col>
      </Row>
    </div>

    <Row>
    <Col className="descCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ padding: 0, margin: 0 }}>
            <Col xs={12} sm={12} md={3} lg={3} xl={3} className="showcase showCol1" style={{ padding: 0, margin: `15px 0 0 0` }}>
                <div className="imgdiv imgborder showcase1" onClick={() => openLink(firstLink)}>
                    <div className="desDiv" style={{padding: `15px` }}>
                        <div>
                        <h3 className="desc">Teaching Through Turmoil</h3>
                        <p className="desc"><i>by Jason Shan</i></p>
                        <p className="descText" style={{ width: `100%` }}>Matangi Rajamani is an 8th grade science and math teacher at Kennedy Middle School. 
                            In the article, Ms. Rajamani gives insight into her strategies for teaching and helping 
                            each individual student and her experience of shelter-in-place.</p>
                        </div>
                    </div>
                </div>
                <div className="firstArticle overlay hoverGray" style={{ margin: 0 }} onClick={() => openLink(firstLink)}></div>
            </Col>
        </Col>
    </Row> */}

    <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Issue No. 2</p>
        </Col>
      </Row>
    </div>

    <Row style={{ margin: 0, padding: 0 }}>
        <Col xs={12} sm={12} md={6} lg={6} xl={6} className="newsCol" style={{ padding: 0, margin: 0, position: `relative` }}>
            <div className="imgdivid imgdiv imgborder" onClick={() => openLink(latestLink)}>
                <Image className="displayImg" src={newsletter2} rounded fluid></Image>
                <div className ="openingInfo overlay" style={{ margin: 0, verticalAlign: `middle` }}>
                  <p><strong>{no2Title}</strong><br></br>{no2Desc}</p>
                </div>
            </div>
        </Col>
        <Col className="descCol" xs={12} sm={12} md={6} lg={6} xl={6} style={{ padding: 0, margin: 0 }}>
            <Col xs={12} sm={12} md={3} lg={3} xl={3} className="showcase showCol1" style={{ padding: 0, margin: `15px 0 0 0` }}>
                <div className="imgdiv imgborder showcase1" onClick={() => openLink(firstLink)}>
                    <div className="imgdiv showcaseimg" id="img1">
                        {/* <Image className="image" src={firstimg} fluid/> */}
                    </div>
                    <div className="desDiv">
                        <div>
                        <h3 className="desc">Teaching Through Turmoil</h3>
                        <p className="desc"><i>by Jason Shan</i></p>
                        <p className="descText">Matangi Rajamani is an 8th grade science and math teacher at Kennedy Middle School. 
                            In the article, Ms. Rajamani gives insight into her strategies for teaching and helping 
                            each individual student and her experience of shelter-in-place.</p>
                        </div>
                    </div>
                </div>
                <div className="firstArticle overlay hoverGray" style={{ margin: 0 }} onClick={() => openLink(firstLink)}></div>
            </Col>
            <Col xs={12} sm={12} md={3} lg={3} xl={3} className="showcase showCol2" style={{ margin: 0, padding: 0 }}>
                <div className="imgdiv imgborder showcase2" onClick={() => openLink(secondLink)} style={{ marginRight: `-15px`, marginLeft: `15px` }}>
                    <div className="imgdiv showcaseimg" id="img2">
                        {/* <Image className="image" src={secondimg} fluid/> */}
                    </div>
                    <div className="desDiv">
                        <div>
                        <h3 className="desc">Behind the Scenes</h3>
                        <p className="desc"><i>by Belinda Chen</i></p>
                        <p className="descText">Board Members Jeff Moe and Rosa Kim reveal the 
                        rationale leading up to the change in grading policies and possible plans 
                        for the upcoming school year. Both Mr. Moe and Ms. Kim encourage 
                        students to use this free time to improve upon themselves for their futures.</p>
                        </div>
                    </div>
                </div>
                <div className="secondArticle overlay hoverGray" style={{ margin: 0 }} onClick={() => openLink(secondLink)}></div>
            </Col>
        </Col>
    </Row>

    <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Issue No. 1</p>
        </Col>
      </Row>
    </div>

    <Row style={{ margin: 0, padding: 0 }}>
        <Col xs={12} sm={12} md={6} lg={6} xl={6} className="newsCol" style={{ padding: 0, marginLeft: 0, position: `relative` }}>
            <div className="imgdivid imgdiv imgborder" onClick={() => openLink(latestLink)}>
                <Image className="displayImg" src={newsletter1} rounded fluid></Image>
                <div className ="openingInfo overlay" style={{ margin: 0, verticalAlign: `middle` }}>
                  <p><strong>{no1Title}</strong><br></br>{no1Desc}</p>
                </div>
            </div>
        </Col>
        <Col className="descCol" xs={12} sm={12} md={6} lg={6} xl={6} style={{ padding: 0, marginTop: `15px` }}>
            <Col xs={12} sm={12} md={6} lg={6} xl={6} className="showcase showCol1" style={{ padding: 0, margin: 0 }}>
                <div className="imgdiv imgborder showcase1" style={{ border: `3px solid black` }}>
                    <div className="imgdiv showcaseimg" id="img3">
                        {/* <Image className="image" src={donations2} fluid/> */}
                    </div>
                    <div className="desDiv" style={{padding: `7px`}}>
                        <div>
                        <h3 className="desc">Five Thousand Thank You's</h3>
                        <p className="desc"><i>by Joseph Shan</i></p>
                        <p className="descText">Uniting to form a donation group, the Lynbrook Chinese American Community has 
                        raised over five thousand dollars, which were all put into effective use. They explain how they are committed 
                        to making a large impact through small actions, and how everyone, even children, can get involved.</p>
                        </div>
                    </div>
                </div>
            </Col>
            <Col xs={12} sm={12} md={6} lg={6} xl={6} className="showcase showCol2" style={{ margin: 0, padding: 0 }}>
                <div className="imgdiv imgborder showcase2" style={{ border: `3px solid black`, marginLeft: `15px`, marginRight: `-15px` }}>
                    <div className="imgdiv showcaseimg" id="img4">
                        {/* <Image className="image" src={donations} fluid/> */}
                    </div>
                    <div className="desDiv">
                        <div>
                        <h3 className="desc">Giving Back, And Giving Lots</h3>
                        <p className="desc"><i>by Belinda Chen</i></p>
                        <p className="descText">During the COVID-19 pandemic, the Tri-Valley Chinese American Committee 
                        came together to donate PPE to many hospitals and essential workers all throughout 
                        the community, and eventually amassed incredible amounts. This effort involved both adults and
                        students.</p>
                        </div>
                    </div>
                </div>
            </Col>
        </Col>
    </Row>

    <div style={{ height: `15px` }}></div>

  </Container>
  )}